import React from 'react';
import {Field, reduxForm} from "redux-form";
import s from "./../messagesItems.module.css"
import {Input} from "../../../common/formsControls/formsControls";
import {maxLengthCreator, required} from "../../../../utils/validations/validator";

const maxLength10=maxLengthCreator(10);

const AddNewMessageForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit} className={s.footerForm}>
            <div className={s.footerText}>
                <Field placeholder={'Write something here...'} name={'newMessageBody'}
                       component={Input} className={s.footerInput}
                validate={[required,maxLength10]}
                />
            </div>
            <div className={s.footerSend}>
                <button>Send</button>
            </div>
        </form>
    )
}

export const AddMessageReduxForm=reduxForm({form:'dialogAddMessageForm'})(AddNewMessageForm);


