import React from "react";
import s from "./../messagesItems.module.css";


const Chat = (props) => {

    return <div className={s.chatContent}>
            <div className={s.message}>
                <p>{props.message}</p>
            </div>
            <div className={s.avaText}>
                <img className={s.ava} src={props.src}/>
                <div>{props.time}</div>
            </div>
    </div>
}

export default Chat;