import React from "react";
import s from "./messagesItems.module.css"
import Chat from "./Chat/Chat";
import {AddMessageReduxForm} from "./AddMessageForm/AddMessageForm";


const MessagesItems = (props) => {

    let messagesElements = props.messagess.map(m => <Chat id={m.id} message={m.message} src={m.src} time={m.time}
                                                          key={m.id}/>)

    let addNewDialogs=(values)=>{
        props.addSendactionCreator(values.newMessageBody);
    }


    return (
        <div className={s.messages}>
            <div className={s.messageHeader}>
                <div className={s.message_header1}>
                    <div className={s.header12}><img
                        src="https://iqonic.design/themes/socialv/html/images/user/05.jpg"/></div>
                    <div className={s.header12}><h5>Team Discussions</h5></div>
                </div>
                <div className={s.message_header2}>
                    <div className={s.header2_icons}>
                        <img className={s.icons1}
                             src="https://cdn.icon-icons.com/icons2/272/PNG/128/Phone_30029.png"/>
                    </div>
                    <div className={s.header2_icons}>
                        <img src="https://img.icons8.com/ultraviolet/2x/camera.png"/>
                    </div>
                    <div className={s.header2_icons}>
                        <img src="https://cdn.icon-icons.com/icons2/2596/PNG/128/delete_one_icon_155508.png"/>
                    </div>
                    <div className={s.header2_icons}>
                        <img src="https://img.icons8.com/fluent/2x/menu-2.png"/>
                    </div>
                </div>
            </div>
            <div className={s.messageContent}>
                {messagesElements}
            </div>
            <div className={s.messageFooter}>
                <div className={s.footerIcons}>
                    <div><img src='https://img.icons8.com/fluent-systems-regular/2x/happy.png'/></div>
                    <div><img src='https://img.icons8.com/ios/2x/attach.png'/></div>
                </div>
                <div><AddMessageReduxForm onSubmit={addNewDialogs}/></div>
            </div>
        </div>
    )
}


export default MessagesItems;
