import React from "react";
import s from "./dialogsInfo.module.css"


const DialogsInfo = () => {
    return (
        <div className={s.dialogs}>
            <div className={s.dialog_profile}>
                <div className={s.dialog_profile1}>
                    <div>
                        <img src="https://iqonic.design/themes/socialv/html/images/user/1.jpg" alt=""/>
                    </div>
                    <div className={s.dialog_profile_text}>
                        <h5>Bni Jordan</h5>
                        <p>Web Designer</p>
                    </div>
                </div>
                <button type="submit" className={s.dialog_search}>
                    <input type="text" className={s.dialog_search1} placeholder="Search..."/>
                </button>
            </div>
        </div>
    )
}
export default DialogsInfo;